/**
 * @file src/styles/footer.less
 */

.footer-wrapper {
    .layoutCentered();
    max-width: @size-layoutWidth;
    padding: 0 0 @size-marginBase;

    .footer {
        .cleared();
    }

    .license-wrapper {
        .fontSmallest();

        .license-badge {
            float: left;
            margin-right: 15px;
            border: 0;
        }
    }
}